<template>
<div class="_1aU_RW padding-top-2rem;background-color:#ffffff">
    <div class="_1VZIbu1">
        <!-- <b-carousel id="carousel-1" v-model="slide" :interval="4000" controls indicators background="#ababab" img-width="1024" img-height="480" style="text-shadow: 1px 1px 2px #333;" @sliding-start="onSlideStart" @sliding-end="onSlideEnd">

            <b-carousel-slide v-for="(data1, index) in data" :key="index" class="img-lazy res-ponsive border-radius" :img-src="'https://go2joylocal.s3-ap-southeast-1.amazonaws.com/'+data1.imagePath" >

            </b-carousel-slide>

        </b-carousel> -->
        <div class="gallery">
            <div v-for="(dataHotel, index) in data" :key="index">
                <div style="padding:0 10px">
                    <el-image class="img-lazy border-radius res-ponsive " :src="'https://go2joylocal.s3-ap-southeast-1.amazonaws.com/'+dataHotel.imagePath" lazy placeholder="Images loading...">
                    </el-image>
                </div>
            </div>

        </div>
        <!-- <slick  :options="slickOptions">
            <div v-for="(dataHotel, index) in data" :key="index">
                <div style="padding:0 10px">
                    <el-image class="img-lazy border-radius res-ponsive " :src="'https://go2joylocal.s3-ap-southeast-1.amazonaws.com/'+dataHotel.imagePath" lazy placeholder="Images loading...">
                    </el-image>
                </div>
            </div>

        </slick> -->
    </div>
</div>
</template>

<script>
import Slick from 'vue-slick';
import axios from 'axios';

export default {
    name: 'Slider',
    components: {
        Slick
    },
    data() {
        return {
            data: {

            },
            slickOptions: {
                slidesToShow: 1,
                arrows: false,
                // Any other options that can be got from plugin documentation
            },

        }
    },

    created() {
        this.getList();
    },
    methods: {
        async getList() {
            axios.defaults.headers = {
                'deviceid': 'device_for_web',
            }

            let {
                data
            } = await axios.get('http://118.69.235.218:8080/hotelapi/home/view/findHomePageInfo');

            let listBanner = data.bannerFormList;
            //console.log('this.data : ', this.data);

            this.data = listBanner
            console.log('this.data.bannerslide', this.data);
        },

    },
    mounted() {
        var that = this
        setTimeout(function () {
            that.images.push('http://devcereal.com/wp-content/uploads/2016/05/URL-URI-URN-whats-difference.png')
            that.images.push('http://www.shawacademy.com/blog/wp-content/uploads/2015/10/URL-1000x605.jpg')

        }, 300)
    },
    updated() {
        $('.gallery').slick({
            autoplay: true,
            dots: false,

        });
    }

}
</script>

<style scoped>
.slick-arrow {
    display: none !important;
}

.slick-dots {
    display: none !important;
}

#carousel-1 {
    border-radius: 30px !important;
}

#carousel-1 .carousel-item {
    border-radius: 30px !important;
}

.img-fluid {
    border-radius: 30px !important;
}

.padding-top-2rem {
    padding-top: 0;
}
@media only screen and (max-width: 374px) and (min-width: 320) {
    ._1VZIbu1 {
    padding: 10rem 0 2rem 0;
}
}
@media only screen and (max-width: 480px) and (min-width: 375) {
    .padding-top-2rem {
        padding-top: 4rem;
    }


    ._1VZIbu1 {
    padding: 10rem 0 2rem 0;
}
}
.style-slider {
    display: block;
    overflow: hidden;
    border-radius: 20px;
    margin: 0 auto 0px auto;
}

.border-radius {
    border-radius: 20px;

}

.res-ponsive {
    width: 100%;

}

.style-slider img {
    width: 100%;

}

.padd-l-r-10 {
    padding: 0 10px 0 10px;
}

.el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
}

.padd-top {
    padding-top: 30px
}

@import 'swiper/css/swiper.css';
@import './base.scss';

.swiper .swiper-pagination-bullet-custom {
    font-size: 20px;
    width: 100px !important;
    height: 100px !important;
    line-height: 100px !important;
    text-align: center;
    color: red;
    opacity: 0.7;
    background: rgba(red, 0.2);
    /* transition: all $transition-time-normal; */
}

.swiper .swiper-pagination-bullet-custom:hover {
    opacity: 1;
}

.swiper .swiper-pagination-bullet-custom .swiper-pagination-bullet-active {
    opacity: 1;
    color: #ffffff;
    background: #007aff;

}

.border-radius {
    border-radius: 20px;
}
</style>



