import Vue from 'vue'
import Slick from 'vue-slick';
import Slider from "./../Home/Slider.vue";
import Specital from '@/components/Home/Specital.vue'
import HotDeal from '@/components/Home/HotDeal.vue'
import i18n from './../../lang/i18n';

import axios from 'axios';
export default {
    name: 'Content',
    components: {
        Specital,
        HotDeal,
        Slick

    },

    data() {
        return {
            data: {

            },
            fullscreenLoading: false,
            slickOptions: {
                slidesToShow: 4,
                arrows: false,
