            },
            slickOptions1: {
                slidesToShow: 2,
                arrows: false,
                // Any other options that can be got from plugin documentation
            },

        }
    },
    // props: ['this.data'],
    async created() {
        axios.defaults.headers = {
            'deviceid': 'device_for_web',
        }
        let {
            data
        } = await axios.get('http://118.69.235.218:8080/hotelapi/home/view/findHomePageInfo');
        console.log('1234')
        let data1 = data.detailCollectionList;
        let HotHotel = (data1[1]);
        console.log('1234', HotHotel)
        let dsHotHotel = HotHotel.hotelFormList
        console.log('1234', dsHotHotel)
        this.data = dsHotHotel
        console.log('1234', this.data)
    },
    methods: {
        chooseSpecitalHotel(dataHotel) {
            this.$emit('dsHotel', dataHotel)
            console.log('1234')
            this.$store.dispatch({
                type: 'updateDataRoom',
                data: this.data
            });
            this.$router.push({
                name: 'Category',
                params: {
                    Sn: 0
                }
            })
        },
        openFullScreen1() {
            this.fullscreenLoading = true;
            setTimeout(() => {
                this.fullscreenLoading = false;
            }, 2000);
        },
    },

    updated() {
        $('.gallery1').slick({
            autoplay: true,
            dots: false,
            slidesToShow: 4,
            slidesToScroll: 4,
            responsive: [

                {
                    breakpoint: 1024,
                    settings: {
                        slidesToShow: 4,
                        slidesToScroll: 4,
                        infinite: true
                    }
                },
                {
                    breakpoint: 600,
                    settings: {
                        slidesToShow: 3,
                        slidesToScroll: 3
                    }
                },
                {
                    breakpoint: 480,
                    settings: {
                        slidesToShow: 2,
                        slidesToScroll: 2
                    }
                }
                // You can unslick at a given breakpoint now by adding:
                // settings: "unslick"
                // instead of a settings object
            ]
        });
        $('.gallery2').slick({
            autoplay: true,
            dots: false,
            slidesToShow: 2,
            slidesToScroll: 2,
            responsive: [

                {
                    breakpoint: 1024,
                    settings: {
                        slidesToShow: 2,
                        slidesToScroll: 2,
                        infinite: true
                    }
                },
                {
                    breakpoint: 600,
                    settings: {
                        slidesToShow: 2,
                        slidesToScroll: 2
                    }
                },
                {
                    breakpoint: 480,
                    settings: {
                        slidesToShow: 1,
                        slidesToScroll: 1
                    }
                }
             
            ]
        });
