import Vue from 'vue';
import VueCarousel from 'vue-carousel';
import axios from 'axios';
Vue.use(VueCarousel);
export default {
    name: 'Specital',
    data() {
        return {
            data: {
                sn: 0
            },
            tempMessage: ''
        }
    },

    async created() {
        axios.defaults.headers = {
            'deviceid': 'device_for_web',
        }
        let {
            data
        } = await axios.get('http://118.69.235.218:8080/hotelapi/home/view/findHomePageInfo');
        let data1 = data.detailCollectionList;
        let HotHotel = (data1[1]);
        let dsHotHotel = HotHotel.hotelFormList
        this.data = dsHotHotel;
        this.sn = dsHotHotel.sn

    },
    methods: {
        chooseSpecitalHotel(dataHotel, sn) {
            this.$emit('dsHotel', dataHotel)
            // console.log('1234')
            this.$store.dispatch({
                type: 'updateDataRoom',
                data: this.data,
                sn: this.sn
            });
            this.$router.push({
                name: 'Category',
                params: {
                    Sn: 2
                }
            })
        },
        sendMessage() {
            this.$store.commit("SET_MESSAGE", this.tempMessage);
            this.tempMessage = '';
        }
    },
    // computed: {
    //     roomData() {
